#pickup_point, #billing_pickup_locations {
    display: none;

    &.custom-pickup-point-section, &.custom-pickup-point-locations-section {
        .displayNone {
            display: none!important;
        }
        .displayInlineBlock {
            display: inline-block!important;
        }
        .blue-cards {
            display: inline-flex;
            > label {
                max-width: 100%;
            }

            .auchan_drumul_taberei {
                margin-right: 0!important;
            }

            .auchan_pitesti {
                margin-left: 0!important;
            }
        }
    }

    &.custom-pickup-point-section {
        .blue-cards {
            display: inline-flex;
            > label {
                &:first-child {
                    margin: 0 8px 0 0!important;
                }
                &:last-child {
                    margin: 0 0 0 8px!important;
                }
            }
        }
    }
}